@import url(https://fonts.googleapis.com/css2?family=Average+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Average Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;  
}

.appWrapper {
  position: relative;
  padding: 50px 20px 0 20px;
  max-width: 900px;
  width: 100%;
}
.offButtonWrapper {
  margin-bottom: -90px;
  height: 200px;
  

}
.customRow {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 700px;
  margin-top: 20px;
}


.donut-complex text {
	font-family: 'Average Sans', sans-serif;
		font-size: 0.4rem;
	font-weight: bold;
	opacity: 0;
	transition: opacity 0.35s ease-in-out 1s;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	color: #000;
	fill: #000;
	cursor: pointer;

}

.donut-complex circle.segmentCircle {
	fill: none;
	cursor: pointer;
	/* transition: stroke-dashoffset 1s ease-in-out; */
}
.donut-complex .segmentGroup:hover circle.segmentCircle {
	stroke-width: 50px;
}
.donut-complex circle.centerCircle {
	fill: #000;
	/* transition: stroke-dashoffset 1s ease-in-out; */
}

.donut-complex:not(.donut-complex--loaded) circle {
	stroke-dashoffset: 0;
}

.donut-complex--loaded text {
	opacity: 1;
}
.CustomValue {
    background-color: #727272;
    padding: 20px 0;
    border-radius: 20px;
}
.CustomValue h3 {
    font-size: 18px;
    color: #fff;
    padding: 0 10px;
    margin: 0;
}
.CustomValue .fields { 
    display: flex;
    flex-direction: row;
}
.CustomValue .fields input {
    padding: 20px;
    font-size: 24px;
    max-width: 100px;
    border-radius: 10px;
    margin: 10px;
}
.CustomValue .setButton {
    margin-left: 10px;
    padding: 5px 20px;
    font-size: 28px;
    background-color: #9b9b9b;
    border-radius: 10px;
    border: 2px solid #000;
    float: left;
    cursor: pointer;
}
.CustomValue .setButton:hover {

    background-color: #efefef;
    }
.CustomValue.selected .setButton {

background-color: #fff;
}
.OffButton {
    position: absolute;
    right: 0;
    top:20px;
    background-color: #727272;
    width: 230px;
    height: 230px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
    cursor: pointer;
}
.OffButton:hover, .OffButton.selected {
    background-color: #fff;
}


.OffButton .innerCircle {
    font-size: 22px;
    background-color: #000;
    color: #fff;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
